import Vue from 'vue'
import App from './App'
import router from './router'
import wanghaoUI  from 'wanghaoUI'

Vue.use(wanghaoUI)

Vue.config.productionTip = false

new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



