import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'Screenfull',
      component: resolve => require(['../components/Screenfull.vue'], resolve),
      meta: {
        title: ''
      }
    },
    {
      path: '/whPieChart',
      name: 'WhPieChart',
      component: resolve => require(['../components/WhPieChart.vue'], resolve),
      meta: {
        title: ''
      }
    },
    {
      path: '/screenfull',
      name: 'Screenfull',
      component: resolve => require(['../components/Screenfull.vue'], resolve),
      meta: {
        title: ''
      }
    }
  ]
})



